@import "src/assets/scss/variables";

.component.-item {
  width: 100%;
  margin-bottom: 20px;
  padding: 12px 16px;
  background: $color-content-bg;
  border-radius: $border-radius-default;
  cursor: pointer;
  transition: $transition-default;

  .image {
    margin-right: 12px;
    transition: $transition-default;
    filter: grayscale(1);
  }

  .content {
    margin-top: 1px;
  }

  .title {
    font-size: 15px;
    line-height: 18px;
  }

  .subtitle {
    margin-top: 2px;
    font-size: 12px;
    line-height: 14px;
  }

  .fields {
    margin-top: 30px;

    & > * {
      &:not(:last-child) {
        margin-bottom: 18px;
      }
    }
  }

  &:hover {
    box-shadow: $box-shadow-default;
    background: $color-content-bg-highlight;

    .image {
      filter: grayscale(0);
    }
  }

  &.-active {
    background: $color-dark;
    color: $color-text-regular;

    .image {
      filter: grayscale(0);
    }
  }
}
